html{background:#000;color:#fff;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol"}body{margin:none}a{color:#fff;text-decoration:none}button{padding:20px 55px;background:#fff;color:black;font-weight:bold;font-size:20px;border:none;box-shadow:#000 5px 5px 0px}button:hover{background:#ffd369}.display-none{display:none}nav{width:100%;display:flex;justify-content:space-evenly;background:#000;height:75px}nav #title-wrapper{align-self:center;justify-self:center;font-weight:bold;text-transform:uppercase;font-size:45px;font-family:'Russo One', sans-serif;text-shadow:2px 2px #000;color:#fff}nav #nav-right{align-self:center;justify-self:flex-end;font-weight:bold;display:flex}nav #nav-right ul,nav #nav-right li{height:75px;display:flex;align-content:center;text-align:center}nav #nav-right ul div,nav #nav-right li div{height:fit-content;align-self:center;margin:20px}nav #nav-right a{display:block;line-height:75px}nav #nav-right li:hover{background-color:#fff;color:#000}nav #nav-right li:hover a{color:#000}#game-container{height:65vh;display:flex;justify-content:center;align-items:center;border-top:solid #fff 1px;border-bottom:solid #fff 1px}#game-window{display:flex;align-items:center;justify-content:center;width:100%;height:100%}#game-window canvas{width:100%;height:100%}#VRButton{bottom:unset !important}footer{display:flex;align-items:center;padding:25px 0px;background:#000;height:20vh}#main-list{display:flex;justify-content:space-evenly;width:100%}#main-list ul>li{padding:5px 0px}#main-list ul>li:first-child{font-weight:bold;font-size:large;color:#fff}#main-list ul>li a{color:#ffd369}#main-list ul>li a:hover{text-decoration:underline}
